<template>
  <div class="gContainer">
    <gSearch @getData="update"/>
    <d3graph
        :graph="graph"
        :goodData="goodData"
        :rateData="rateData"
        :viewData="viewData"
        :input="input"
    />

  </div>
</template>

<script>
import gSearch from '@/components/gSearch.vue'
import d3graph from '@/components/d3graph.vue'

export default {
  components: {
    gSearch,
    d3graph
  },
  data () {
    return {
      graph: null,
      goodData: null,
      rateData: null,
      viewData: null,
      input: null
    }
  },

  methods: {
    // 视图更新
    update (data, goodData, rateData, viewData, input) {
      // console.log('update')
      this.input = input
      this.viewData = viewData
      this.rateData = rateData
      this.goodData = goodData
      this.graph = data
    }
  }
}
</script>

