<template>
  <div style="margin-top: 40px;width: 500px;">
    <el-autocomplete style="width: 480px"
                     class="inline-input"
                     v-model="input"
                     :fetch-suggestions="querySearch"
                     placeholder="请输入节点名称"
                     :trigger-on-focus="false"
                     @select="handleSelect"
                     clearable
                     @clear="setBlur()"
                     @keyup.enter.native="query"
                     ref="autocomplete"
    >
      <el-select v-model="mode" slot="prepend" placeholder="关键字查询">
        <el-option label="课程查询" value="课程"></el-option>
        <el-option label="学校查询" value="学校"></el-option>
        <el-option label="课程类别查询" value="课程分类"></el-option>
      </el-select>

      <el-button slot="append" type="success" icon="el-icon-search" @click="query"></el-button>
    </el-autocomplete>
  </div>
</template>

<script>
import { Loading } from 'element-ui'

export default {
  name: 'gSearch',

  data () {
    return {
      input: null,
      mode: '课程',
      data: null,
      recommendGoodData: null,
      recommendRateData: null,
      recommendViewData: null,
      results: []
    }
  },
  mounted () {
    this.loadAll()
  },
  methods: {

    // 查询成功通知
    open1 () {
      this.$notify({
        title: '查询成功',
        message: '共' + this.data.nodes.length + '个节点，' + this.data.links.length + '条关系。',
        type: 'success'
      })
    },

    // 查询无结果通知
    open4 () {
      this.$notify.error({
        title: '查询失败',
        message: '无结果'
      })
    },

    // 获取查询数据
    getSearchData () {
      this.$http.get('http://120.78.220.240:9090/search', {
        params: {
          c: this.mode,
          n: this.input
        }
      }).then((res) => {
        this.data = res.data

        // 获取推荐数据Good
        this.getRecommendGood()
      }, (err) => {
        console.log(err)
      })
    },

    // 获取推荐数据Good
    getRecommendGood () {
      this.$http.get('http://120.78.220.240:9090/recommend-good', {
        params: {
          c: this.mode,
          n: this.input
        }
      })
        .then((res) => {
          this.recommendGoodData = res.body[0]

          // 获取推荐数据Rate
          this.getRecommendRate()
        }, (err) => {
          console.log(err)
        })
    },

    // 获取推荐数据Rate
    getRecommendRate () {
      this.$http.get('http://120.78.220.240:9090/recommend-rate', {
        params: {
          c: this.mode,
          n: this.input
        }
      })
        .then((res) => {
          this.recommendRateData = res.body[0]

          // 获取推荐数据view
          this.getRecommendView()
        }, (err) => {
          console.log(err)
        })
    },

    // 获取推荐数据View
    getRecommendView () {
      this.$http.get('http://120.78.220.240:9090/recommend-view', {
        params: {
          c: this.mode,
          n: this.input
        }
      })
        .then((res) => {
          this.recommendViewData = res.body[0]

          // 关闭加载动画
          this.loadingInstance.close()
          this.$emit('getData', this.data, this.recommendGoodData, this.recommendRateData, this.recommendViewData, this.input)

          // 显示通知
          if (this.data.nodes.length !== 0 && this.data.links.length !== 0) {
            this.open1()
          } else this.open4()
        }, (err) => {
          console.log(err)
        })
    },

    query () {
      // console.log('query')
      // 回车和搜索按钮后把suggestions列表置空
      this.$refs.autocomplete.suggestions = []
      // 获取数据
      if (this.input !== null && this.input !== undefined) {
        // 开启加载动画
        this.loadingInstance = Loading.service({
          lock: true,
          text: '拼命加载中',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)',
          target: '.svg'
        })
        this.getSearchData()
      }
    },

    querySearch (queryString, cb) {
      var res = this.results
      var results = queryString ? res.filter(this.createFilter(queryString)) : res
      // 调用 callback 返回建议列表的数据
      cb(results)
    },
    createFilter (queryString) {
      return (res) => {
        return (res.value.toLowerCase().indexOf(queryString.toLowerCase()) !== -1)
      }
    },
    // 加载建议数据
    loadAll () {
      this.$http.get('http://120.78.220.240:9090/name')
        .then((res) => {
          // console.log('loadAll')
          this.results = res.body
        }, (err) => {
          console.log(err)
        })
    },
    handleSelect (item) {
      // console.log(item)
    },
    setBlur () {
      // 在点击由 clearable 属性生成的清空按钮时，主动触发失去焦点，解决‘fetch-suggestions’输入建议不提示的bug
      document.activeElement.blur()
    }
  }
}
</script>

<style lang='scss' scoped>

.input-with-select .el-input-group__prepend {
  background-color: #6ecbf3;
}

::v-deep .el-input-group__prepend {
  border-right: 0;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  .el-select {
    width: 130px;
    background-color: #fff;
    border-radius: 15px;
  }
}

::v-deep .el-input-group__append {
  border-left: 0;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}
</style>
