<script>
import * as d3 from 'd3'
import install from '@/plugins/d3-context-menu'

install(d3) // 为d3注册右键菜单插件

export default {
  name: 'd3graph',
  props: {
    graph: {
      type: Object
    },
    goodData: {
      type: Object
    },
    rateData: {
      type: Object
    },
    viewData: {
      type: Object
    },
    input: {}
  },
  data () {
    return {
      names: ['课程类别', '学校', '课程'],
      labels: ['Category', 'University', 'Course'],
      // linkTypes: ['', '开设', '属于'],
      svgDom: null, // svg的DOM元素 => d3.select('#svg1')
      nodeState: 0,
      // 文本状态，表示是否显示文本信息（0：显示/1：不显示）
      textState: 0,
      // d3render()最终展示到页面上的数据（节点隐藏功能）
      nodes: [],
      links: [],
      /* eslint-disable */
      // 自定义图例及颜色（数组保证一一对应）
      // colors		图例颜色（9个颜色）
      // states   图例状态（on：显示 / off：不显示）
      colors: ['#ca635f', '#6ad999', '#57a6f3', '#cece23'],
      states: [],
      selectNodeData: {}, // 选中节点的详细信息展示
      isNodeClicked: false, // 是否点击（选中）节点

      // 右击事件的菜单栏
      menu: [
        {
          title: '隐藏节点',
          action: (elm, d) => {
            console.log(d)
            // 遍历删除节点
            this.nodes = this.nodes.filter(node => {
              if (node.id === d.id) return false
              else return true
            })
            // 遍历删除关系
            this.links = this.links.filter(link => {
              if (link.source.id === d.id || link.target.id === d.id) return false
              else return true
            })
            this.d3render() // 重新渲染图
            this.stateInit()
          },
          disabled: false // optional, defaults to false
        },
        {
          title: '显示节点关联图',
          action: (elm, d) => {
            console.log(d)
            // 遍历保留对应节点
            this.nodes = this.graph.nodes.filter(node => {
              if (node.id === d.id) return true
              else {
                for (var i = 0; i < this.graph.links.length; i++) {
                  // 如果links的起点等于name，并且终点等于正在处理的则显示
                  if (this.graph.links[i].source.id === node.id && this.graph.links[i].target.id === d.id) {
                    return true
                  }
                  if (this.graph.links[i].target.id === node.id && this.graph.links[i].source.id === d.id) {
                    return true
                  }
                }
                return false
              }
            })
            // 遍历保留节点的关联关系
            this.links = this.graph.links.filter(link => {
              if (link.source.id === d.id || link.target.id === d.id) return true
              else return false
            })
            this.d3render() // 重新渲染图
            this.stateInit()
          }
        },
        {
          title: '显示所有查询节点',
          action: (elm, d) => {
            this.nodes = this.graph.nodes
            // 遍历保留节点的关联关系
            this.links = this.graph.links
            this.d3render() // 重新渲染图
            this.stateInit()
          }
        }
      ],
    }
  },

  watch: {
    goodData(newData, oldData) {
      // console.log("good")
      // console.log(newData, oldData)
    },
    rateData(newData, oldData) {
      // console.log("rate")
      // console.log(newData, oldData)
    },
    viewData(newData, oldData) {
      // console.log("view")
      // console.log(newData, oldData)
    },
    // 当请求到新的数据时，重新渲染
    graph(newData, oldData) {
      // console.log('watch')
      // console.log(newData, oldData)
      // 移除svg和元素注册事件，防止内存泄漏
      this.svgDom.on('.', null);
      this.svgDom.selectAll('*').on('.', null);
      this.selectNodeData = undefined;
      this.d3init()
    }
  },

  created() {
  },

  mounted() {
    this.$http.get('http://120.78.220.240:9090/graph')
        .then((res) => {
          this.graph = res.data;
          // console.log('get');
          // console.log(this.graph);
          this.d3init();
        }, (err) => {
          console.log(err);
        })
  },

  beforeDestroy() {
    // 移除svg和元素注册事件，防止内存泄漏
    this.svgDom.on('.', null)
    this.svgDom.selectAll('*').on('.', null)
  },

  methods: {
    //推荐卡片鼠标进入事件
    mouseenterCard(recommendData) {
      this.selectNodeData = recommendData;
      this.$set(this.selectNodeData, 'class', "Course")
      this.changeGraphStyle(this.selectNodeData.id)
    },

    //隐藏该类型的所有节点（图例）
    hideNodeOfType(event) {
      if (this.nodes.length === this.graph.nodes.length
          || this.states.some((state) => state === 'off')) {
        // console.log(event.target.dataset)
        const index = event.target.dataset.index
        const state = event.target.dataset.state
        // const nodeTypes = ['Enterprise', 'Type', 'Region', 'Country']
        // const linkTypes = ['', 'type', 'locate', 'export']
        // 图例的状态切换（对应类型的节点隐藏）
        if (state === 'on') {
          // 隐藏该类型的所有节点及关联关系
          // this.states[index] = 'off'
          this.$set(this.states, index, 'off')
        } else {
          // this.states[index] = 'on'
          this.$set(this.states, index, 'on')
        }
        /**************************************
         * 状态更新后，同时对数据更新
         */
        const indexs = this.states.map(s => {
          if (s === 'on') {
            return '1'
          } else {
            return '0'
          }
        })
        // 遍历删除节点
        this.nodes = this.graph.nodes.filter(node => {
          for (let i = 0; i < indexs.length; i++) {
            if (node.class === this.labels[i] && indexs[i] === '0') return false
          }
          return true
        })
        // 遍历删除关系
        this.links = this.graph.links.filter(link => {
          for (let i = 0; i < indexs.length; i++) {
            if (i === 0 && indexs[i] === '0') return false
            else if (link.type === this.relation && indexs[i] === '0') return false
          }
          return true
        })
        // 调试时使用
        // console.log(indexs)
        // console.log(this.data.nodes.length, this.data.links.length)
        // console.log(this.nodes.length)
        // console.log(this.links.length)
        // 重新渲染
        this.d3render()
      } else {
        this.$message.error('展示全部节点时才能隐藏图例')
      }
    },

    //关系图初始化
    d3init() {
      // console.log('d3init')
      // console.log(this.graph)
      this.links = this.graph.links
      this.nodes = this.graph.nodes
      this.svgDom = d3.select('#svg')  // 获取svg的DOM元素
      this.d3render()
      // 数据状态初始化
      this.stateInit()
    },
    // 数据状态初始化
    stateInit() {
      this.nodeState = 0
      this.textState = 0
      this.states = Array(this.names.length).fill('on')
    },

    d3render() {
      const _this = this; // 临时获取Vue实例，避免与d3的this指针冲突

      // 渲染前清空svg内的元素
      _this.svgDom.selectAll('*').remove()
      // svg.selectAll('g').remove()

      var svg = _this.svgDom
          .on('click', () => {
            // console.log(this.isNodeClicked)

            this.isNodeClicked = false

            // 移除所有样式
            this.clearGraphStyle()

          })
          // 给画布绑定zoom事件（缩放、平移）
          .call(d3.zoom().on('zoom', event => {
            // console.log(event)
            var scale = event.transform.k,
                translate = [event.transform.x, event.transform.y]

            svg.attr('transform', 'translate(' + translate[0] + ', ' + translate[1] + ') scale(' + scale + ')');
          }))
          .append('g')
          .attr('width', '100%')
          .attr('height', '100%')

      //绘制箭头
      this.addMarkers()

      // 定义碰撞检测模型
      var forceCollide = d3.forceCollide()
          .radius(d => {
            return 15 * 3
          })
          .iterations(0.15)
          .strength(0.75)

      // 利用d3.forceSimulation()定义关系图 包括设置边link、排斥电荷charge、关系图中心点
      var simulation = d3.forceSimulation(this.nodes)
          .force("link", d3.forceLink().id(d => d.id))
          .force("charge", d3.forceManyBody().strength(-30))
          .force("center", d3.forceCenter(svg.node().parentElement.clientWidth / 2,
              svg.node().parentElement.clientHeight / 2))
          .force("collision", forceCollide)

      // D3映射数据至HTML中
      // g用于绘制所有边,selectALL选中所有的line,并绑定数据data(graph.links),enter().append("line")添加元素
      // 数据驱动文档,设置边的粗细
      var link = svg.append("g")
          .attr("class", "links")
          .selectAll("line")
          .data(this.links)
          .enter()
          .append("line")
          .attr("stroke-width", function (d) {
            // 每次访问links的一项数据
            return 2 //所有线宽度均为2
          })
          .join("path")
          .attr("marker-end", "url(#posMarker)")

      var linksName = svg.append("g")
          .attr("class", "linkTexts")
          .selectAll("text")
          .data(this.links)
          .join("text")
          .style('text-anchor', 'middle')
          .style('fill', '#fff')
          .style('font-size', '8px')
          .text(d => d.relation) //添加关系名


      // 添加所有的点
      // selectAll("circle")选中所有的圆并绑定数据,圆的直径为d.size
      // 再定义圆的填充色,同样数据驱动样式,圆没有描边,圆的名字为d.id
      // call()函数：拖动函数,当拖动开始绑定dragstarted函数，拖动进行和拖动结束也绑定函数
      var node = svg.append("g")
          .attr("class", "nodes")
          .selectAll("circle")
          .data(this.nodes)
          .enter()
          .append("circle")
          .attr("r", function (d) {  //节点大小
            // 每次访问nodes的一项数据
            let size = 10
            switch (d.class) {
              case _this.labels[0]:
                break;
              case _this.labels[1]:
                size = 11;
                break;
              case _this.labels[2]:
                size = 12;
                break;
              default:
                size = 10;
                break;
            }
            return size * 2
          })
          .attr("fill", d => {   //填充颜色
            for (let i = 0; i < this.labels.length; i++) {
              if (d.class === this.labels[i]) return this.colors[i]
            }
          })
          .attr("stroke", "none")
          .attr("name", d => d.name)
          .attr("id", d => d.id)
          .call(this.drag(simulation))
          .on("click", nodeClick)
          .on('mouseenter', function (event) {
            // console.dir(this)
            const node = d3.select(this)
            _this.selectNodeData = node._groups[0][0].__data__
            // console.log(_this.selectNodeData)
            // 遍历节点，并调整图的样式
            _this.changeGraphStyle(_this.selectNodeData.id)
          })
          .on('mouseleave', event => {
            // console.log(this.isNodeClicked)
            if (!this.isNodeClicked) {
              this.clearGraphStyle()
            }
          })
          // .on('contextmenu', d3.contextMenu(this.menu))

      // 显示所有的文本
      // 设置大小、填充颜色、名字、text()设置文本
      // 使用 attr("text-anchor", "middle")设置文本居中
      var text = svg.append("g")
          .attr("class", "texts")
          .selectAll("text")
          .data(this.nodes)
          .enter()
          .append("text")
          .attr("font-size", () => 10)
          .attr("fill", () => '#fff')
          .attr('name', d => d.name)
          .attr("text-anchor", "middle")
          .attr('x', function (d) {
            return textBreaking(d3.select(this), d.name)
          })
          .call(this.drag(simulation))
          .on("click", nodeClick)
          .on('mouseenter', function (event) {
            // console.dir(this)
            const text = d3.select(this)
            _this.selectNodeData = text._groups[0][0].__data__

            _this.changeGraphStyle(_this.selectNodeData.id)
          })
          .on('mouseleave', (event) => {
            if (!this.isNodeClicked) {
              this.clearGraphStyle()
            }
          })
          // .on('contextmenu', d3.contextMenu(this.menu))

      // 圆增加title
      node.append("title").text(d => d.name)

      // simulation中ticked数据初始化并生成图形
      simulation.on("tick", ticked)

      simulation.force("link")
          .links(this.links)
          .distance(d => { // 每一边的长度
            let distance = 15
            switch (d.source.class) {
              case _this.labels[0]:
                distance += 30;
                break;
              case _this.labels[1]:
                distance += 25;
                break;
              case _this.labels[2]:
                distance += 22;
                break;
              default:
                distance += 20;
                break;
            }
            switch (d.target.class) {
              case _this.labels[0]:
                distance += 30;
                break;
              case _this.labels[1]:
                distance += 25;
                break;
              case _this.labels[2]:
                distance += 22;
                break;
              default:
                distance += 20;
                break;
            }
            return distance * 2
          })

      /******************************************
       * 内部功能函数
       * 包括：ticked、文本分隔、节点和文本的点击事件
       */
      // ticked()函数确定link线的起始点x、y坐标 node确定中心点 文本通过translate平移变化
      function ticked() {
        link
            .attr("x1", d => d.source.x)
            .attr("y1", d => d.source.y)
            .attr("x2", d => d.target.x)
            .attr("y2", d => d.target.y)

        linksName
            .attr('transform', d => {
              let x = Math.min(d.source.x, d.target.x) + Math.abs(d.source.x - d.target.x) / 2
              let y = Math.min(d.source.y, d.target.y) + Math.abs(d.source.y - d.target.y) / 2 - 1
              // tanA = a / b
              // A = arctan(tanA)
              let tanA = Math.abs(d.source.y - d.target.y) / Math.abs(d.source.x - d.target.x)
              let angle = Math.atan(tanA) / Math.PI * 180
              // let angle = Math.atan2(1,1)/Math.PI*180
              // console.log(angle)
              // 第一、二象限额外处理
              if (d.source.x > d.target.x) {
                // 第二象限
                if (d.source.y <= d.target.y) {
                  angle = -angle
                }
                // else {  // 第三象限
                //   angle = angle
                // }
              } else if (d.source.y > d.target.y) {
                // 第一象限
                angle = -angle
              }
              return 'translate(' + x + ',' + y + ')' + 'rotate(' + angle + ')'
            })

        node
            .attr("cx", d => d.x)
            .attr("cy", d => d.y)

        text.attr('transform', function (d) {
          let size = 15
          switch (d.class) {
            case _this.labels[0]:
              break;
            case _this.labels[1]:
              size = 14;
              break;
            case _this.labels[2]:
              size = 13;
              break;
            default:
              size = 12;
              break;
          }
          size -= 5
          return 'translate(' + (d.x - size / 2 + 3) + ',' + (d.y + size / 2) + ')'
        })
      }

      /**
       * 文本分隔（根据字数在当前选择器中分隔三行，超过10字省略）
       * @method textBreaking
       * @param {d3text} 文本对应的DOM对象
       * @param {text} 节点名称的文本值
       * @return {void}
       */
      function textBreaking(d3text, text) {
        const len = text.length
        if (len <= 3) {
          d3text.append('tspan')
              .attr('x', 0)
              .attr('y', 2)
              .text(text)
        } else {
          const topText = text.substring(0, 3)
          const midText = text.substring(3, 7)
          let botText = text.substring(7, len)
          let topY = -16
          let midY = 0
          let botY = 16
          if (len <= 7) {
            topY += 10
            midY += 10
          } else if (len > 10) {
            botText = text.substring(7, 9) + '...'
          }

          d3text.text('')
          d3text.append('tspan')
              .attr('x', 0)
              .attr('y', topY)
              .text(function () {
                return topText
              })
          d3text.append('tspan')
              .attr('x', 0)
              .attr('y', midY)
              .text(function () {
                return midText
              })
          d3text.append('tspan')
              .attr('x', 0)
              .attr('y', botY)
              .text(function () {
                return botText
              })
        }
      }

      // 分别定义节点和文本的点击事件
      // 优化：由于点击前必定触发mouseenter事件，所以不用再去查找节点id
      //      直接根据this.selectNodeData拿到节点信息
      // 优化后：只需定义一个点击事件即可
      function nodeClick(event, d) {
        event.cancelBubble = true
        event.stopPropagation() // 阻止事件冒泡

        const id = _this.selectNodeData.id
        _this.isNodeClicked = true
        _this.changeGraphStyle(id)

        return false
      }
    },

    // 根据当前节点名称来更改图样式
    changeGraphStyle(id) {
      // console.log(id)
      // console.log(this.isNodeClicked)
      // 选择#svg1 .nodes中所有的circle，再增加个class
      this.svgDom.select('.nodes').selectAll('circle').attr('class', d => {

        // 节点属性name是否等于name，返回fixed（激活选中当前节点样式）
        if (d.id === id) {
          // console.log('fixed')
          return 'fixed' //当前节点选中样式
        }

        // 当前节点返回空，否则其他节点循环判断是否被隐藏起来(CSS设置隐藏)
        else {
          // links链接的起始节点进行判断,如果其id等于name则显示这类节点
          // 注意: graph = data
          for (var i = 0; i < this.links.length; i++) {
            // 如果links的起点等于name，并且终点等于正在处理的则显示
            if (this.links[i]['source'].id === id && this.links[i]['target'].id === d.id) {
              return 'active'
            }
            if (this.links[i]['target'].id === id && this.links[i]['source'].id === d.id) {
              return 'active'
            }
          }
          return this.isNodeClicked ? 'inactive' : ''
        }
      })

      // 处理相邻的文字是否隐藏
      this.svgDom.select('.texts').selectAll('text')
          .attr('class', d => {
            // 节点属性name是否等于name，返回fixed（激活选中样式）
            if (d.id === id) {
              return ''
            }
            // 当前节点返回空，否则其他节点循环判断是否被隐藏起来(CSS设置隐藏)
            else {
              // links链接的起始节点进行判断,如果其id等于name则显示这类节点
              // 注意: graph = data
              for (var i = 0; i < this.links.length; i++) {
                // 如果links的起点等于name，并且终点等于正在处理的则显示
                if (this.links[i]['source'].id === id && this.links[i]['target'].id === d.id) {
                  return ''
                }
                if (this.links[i]['target'].id === id && this.links[i]['source'].id === d.id) {
                  return ''
                }
              }
              return this.isNodeClicked ? 'inactive' : ''
            }
          })
      // 处理相邻的边line是否隐藏 注意 ||
      this.svgDom.select(".links").selectAll('line')
          .attr('class', d => {
            if (d.source.id === id || d.target.id === id) {
              return 'active'
            } else {
              return this.isNodeClicked ? 'inactive' : ''
            }
          })
          .attr('marker-end', d => {
            if (d.source.id === id || d.target.id === id) {
              return 'url(#posActMarker)'
            } else {
              return 'url(#posMarker)'
            }
          })
      // 处理相邻的边上文字是否隐藏 注意 ||
      this.svgDom.select(".linkTexts").selectAll('text')
          .attr('class', d => {
            if (d.source.id === id || d.target.id === id) {
              return 'active'
            } else {
              return this.isNodeClicked ? 'inactive' : ''
            }
          })
    },

    clearGraphStyle() {
      // console.log('clear')
      // 移除所有样式
      this.svgDom.select('.nodes').selectAll('circle').attr('class', '')
      this.svgDom.select(".texts").selectAll('text').attr('class', '')
      this.svgDom.select('.links').selectAll('line').attr('class', '').attr('marker-end', 'url(#posMarker)')
      this.svgDom.select(".linkTexts").selectAll('text').attr('class', '')
    },

    //拖动函数
    drag(simulation) {
      function dragsubject(event) {
        return simulation.find(event.x, event.y);
      }

      function dragstarted(event) {
        if (!event.active) simulation.alphaTarget(0.3).restart();
        event.subject.fx = event.subject.x;
        event.subject.fy = event.subject.y;
      }

      function dragged(event) {
        event.subject.fx = event.x;
        event.subject.fy = event.y;
      }

      function dragended(event) {
        if (!event.active) simulation.alphaTarget(0);
        // 注释以下代码，使拖动结束后固定节点
        event.subject.fx = null;
        event.subject.fy = null;
      }

      return d3.drag()
          .subject(dragsubject)
          .on("start", dragstarted)
          .on("drag", dragged)
          .on("end", dragended)
    },


    // 绘制关系箭头
    addMarkers() {
      // 定义箭头的标识
      var defs = this.svgDom.append("defs")
      const posMarker = defs.append("marker")
          .attr("id", "posMarker")
          .attr("orient", "auto")
          .attr("stroke-width", 2)
          .attr("markerUnits", "strokeWidth")
          .attr("markerUnits", "userSpaceOnUse")
          .attr("viewBox", "0 -5 10 10")
          .attr("refX", 31)
          .attr("refY", 0)
          .attr("markerWidth", 12)
          .attr("markerHeight", 12)
          .append("path")
          .attr("d", "M 0 -5 L 10 0 L 0 5")
          .attr('fill', '#e0cac1')
          .attr("stroke-opacity", 0.6);
      const posActMarker = defs.append("marker")
          .attr("id", "posActMarker")
          .attr("orient", "auto")
          .attr("stroke-width", 2)
          .attr("markerUnits", "strokeWidth")
          .attr("markerUnits", "userSpaceOnUse")
          .attr("viewBox", "0 -5 10 10")
          .attr("refX", 31)
          .attr("refY", 0)
          .attr("markerWidth", 12)
          .attr("markerHeight", 12)
          .append("path")
          .attr("d", "M 0 -5 L 10 0 L 0 5")
          .attr('fill', '#1E90FF')
          .attr("stroke-opacity", 0.6);
      // const negMarker = defs.append("marker")
      //   .attr("id","negMarker")
      //   .attr("orient","auto")
      //   .attr("stroke-width",2)
      //   .attr("markerUnits", "strokeWidth")
      //   .attr("markerUnits", "userSpaceOnUse")
      //   .attr("viewBox", "0 -5 10 10")
      //   .attr("refX", -25)
      //   .attr("refY", 0)
      //   .attr("markerWidth", 12)
      //   .attr("markerHeight", 12)
      //   .append("path")
      //   .attr("d", "M 10 -5 L 0 0 L 10 5")
      //   .attr('fill', '#999')
      //   .attr("stroke-opacity", 0.6);
    }
  }
}
</script>

